import "./index.scss"
import React from "react";
import {Menu,Icon} from "antd";
const {SubMenu}=Menu;
export class AppSider extends React.Component{
    render(){
        return (
            <Menu
                className="app-side-menu"
                mode="inline"
            >
                <SubMenu className="app-side-menu-recommend" key="sub1" title={<span><Icon type="findMusic"/><span></span></span>}>
                    <Menu.Item key="1"></Menu.Item>
                    <Menu.Item key="2"></Menu.Item>
                </SubMenu>
                <SubMenu key="sub2" title={<span><Icon type="user"/><span></span></span>}>
                    <Menu.Item key="3">Option 9</Menu.Item>
                    <Menu.Item key="4">Option 10</Menu.Item>
                </SubMenu>
            </Menu>
        )
    }
}

export default AppSider;


